* {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.app {
    height: 100vh;
    width: 100%;
    background: -webkit-linear-gradient(#b2dbbf 50%, #f3ffbd 100%);
    background: linear-gradient(#b2dbbf 50%, #f3ffbd 100%);
    color: #333333;
    -webkit-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
}

.app.dark-mode {
    background: -webkit-linear-gradient(#0b0e0c 0%, #0e5d75 100%);
    background: linear-gradient(#0b0e0c 0%, #0e5d75 100%);
    color: #f6f6f6;
    -webkit-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
}

.body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
}
.app.about {
    height: auto;
}

.app.about .body {
    height: auto;
}

p .logo-block img {
    height: auto;
    width: 50px;
    margin: 10px;
}

.dark-mode p .logo-block img{
    background-color: #ffffff;
}

.content p {
    width: 80%;
    margin: 10px auto;
}

p .logo-block {
    display: flex;
    align-items: center;
    justify-content: center;
}

p .progress-bar {
    display: block;
    width: 100%;
    height: 20px;
    margin: 5px 0;
    background-color: blue;
    position: relative;
}

.progress-bar .progress {
    position: absolute;
    top: 0;
    height: 100%;
    background-color: white;
    left: 0;
}

.progress.w-10{
    width: 10%;
}
.progress.w-20{
    width: 20%;
}
.progress.w-30{
    width: 30%;
}
.progress.w-40{
    width: 40%;
}
.progress.w-50{
    width: 50%;
}
.progress.w-60{
    width: 60%;
}
.progress.w-70{
    width: 70%;
}
.progress.w-80{
    width: 80%;
}
.progress.w-90{
    width: 90%;
}
.progress.w-100{
    width: 100%;
}
.progress.learn {
    left: 35%;
    width: auto;
    padding: 5px;
    color: #333333;
}

p .progress-bar.learn-mode {
    height: 30px;
}

.about-section h3 {
    margin: 15px;
    padding: 10px;
    text-decoration: underline;
}

.about-section li {
    list-style: none;
}

.other-details {
    max-width: 300px;
    margin: 0 auto;
}

.links img {
    height: auto;
    width: 50px;
    margin: 10px;
}

.links li {
    display: inline-block;
}

.links {
    margin-bottom: 24px;
}

@media screen and (min-width: 640px) { 

    .skillset {
        width: 100%;
    }

    .skillset ul {
        display: flex;
        flex-wrap: wrap;
    }

    .skillset ul li {
        width: 30%;
        margin: 2%;
    }

    .skillset ul li:nth-child(3n) {
        margin-right: 0;
    }

    p .logo-block {
        min-height: 91px;
    }

    .other-details {
        min-width: 100%;
    }

}
.body.timeline {
    align-items: unset;
}

.timeline h3 {
    margin: 15px;
    padding: 10px;
} 

.timeline .content {
    width: 100%;
}

.timeline-list {
    position: relative;
    height: 88vh;
    overflow: scroll;
}

.timeline-block {
    position: relative;
    margin: 0 20px;
    border-left: 1px solid;
    padding: 0 20px 20px;
    height: 170px;
}

p.year {
    margin-bottom: 10px;
    text-decoration: underline;
}

.app.dark-mode p.desc a {
    color: #008000;
}
p.desc a {
    text-decoration: none;
}

.app .timeline-block::before {
    content: "";
    position: absolute;
    background-color: #333333;
    height: 20px;
    width: 20px;
    top: 0;
    left: -10px;
    border-radius: 50%;
}

.app .timeline-block:last-child::after {
    content: "";
    position: absolute;
    background-color: #333333;
    height: 20px;
    width: 20px;
    bottom: 0;
    left: -10px;
    border-radius: 50%;
}
.app.dark-mode .timeline-block::before {
    background-color: #f6f6f6;
}

.app.dark-mode  .timeline-block:last-child::after {
    background-color: #f6f6f6;
}

@media screen and (min-width: 640px) {
    .timeline-list {
        max-width: 500px;
        margin: 0 auto;
        display: inline-block;
        height: auto;
        overflow: hidden;
    }
    .app.timeline {
        height: auto;
    }
    .app.timeline .body.timeline {
        height: auto;
    }
}
